import React from 'react';
import {connect} from 'react-redux';
import {userLogin,userRegister} from '../redux/user.redux';
import {getRoomList} from '../redux/roomlist.redux';
import {sendUserLoginMsg,getchatmsglist,getFileList,getGroupFileList,recvOnlineNotice} from '../redux/chatmsg.redux';
import {getusermsglist,getuserlist,recvMymsg,recvNewFriend,getusermsgid} from '../redux/usermsg.redux';
// import headUrl from '../img/head4.jpg';
import {Card,Button,Modal,Input,Tabs,message,Icon} from 'antd';
const TabPane = Tabs.TabPane;

@connect(
    state=>state,
    {getusermsgid,userLogin,userRegister,getchatmsglist,getRoomList,sendUserLoginMsg,getusermsglist,getuserlist,recvMymsg,getGroupFileList,recvNewFriend,getFileList,recvOnlineNotice}
)
class Login extends React.Component{

    constructor(props){
        super(props);
        this.state={
            ModalText: '登录',
            visible: false,
            tabpanel:"1",
            username:"",
            password:"",
            registername:"",
            registerpwd:""
        }
    }

    onValueChange(e){
        let name = e.target.name,
            value = e.target.value.trim();
        this.setState({
            [name] : value
        })
    }

    showModal = () => {
        this.setState({
          visible: true,
        });
      }

    handleOk = () => {
        
        let islogin = this.state.tabpanel;
        if(islogin === "1"){
            let logindata = {
                "username": this.state.username,
                "password": this.state.password
                // "reconnect":false
            }

            if (logindata.username.length === 0 || logindata.password.length === 0){
                message.warning("用户名或密码不能为空！")
            }else{
                this.props.userLogin(logindata)
                //监听socket
                this.props.sendUserLoginMsg(logindata)
                this.props.recvMymsg()
                this.props.recvNewFriend()
                this.props.getFileList()
                this.props.recvOnlineNotice()
                this.props.getusermsgid()
                
                this.setState({
                    confirmLoading: true,
                });
        
                setTimeout(() => {
                    if (this.props.user.username !== "") {
                        //获取用户群聊的消息列表：
                        this.props.getchatmsglist(this.state.username)
                        //获取用户的文件列表
                        this.props.getGroupFileList(this.state.username)
                        //获取用户的聊天消息列表：
                        this.props.getusermsglist(this.state.username)
                        this.props.getRoomList(this.state.username)
                        this.props.getuserlist(this.state.username)
                    }
        
                    this.setState({
                    visible: false,
                    confirmLoading: false,
                    username:"",
                    password:""
                    });
                }, 500);
            }
        }else if(islogin === "2"){
            let logindata = {
                "registername" :  this.state.registername,
                "registerpwd"  :  this.state.registerpwd,
                "headimg"      :  this.props.user.headimg
            }
    
            if(logindata.registername.length === 0 || logindata.registerpwd.length === 0 || logindata.headimg.length === 0){
                message.warning("用户名或密码不能为空！")
            }else{
                this.props.userRegister(logindata)

                this.setState({
                    confirmLoading: true,
                    registername:"",
                    registerpwd:""
                });
        
                setTimeout(() => {
                    this.setState({
                    visible: false,
                    confirmLoading: false,
                    });
                }, 500);
            }
        }  
    }

    handleCancel = () => {
    this.setState({
        visible  : false,
        tabpanel : "1"
    });
    }

    callback =(key)=>{
        this.setState({
           tabpanel:key 
        })
    }

    render(){
        return(
            <div>
                <Card style={{ width: 225 }}>
                    {
                        this.props.user.isAuth?        
                        <div className="login">
                            <img src={this.props.user.headimg} alt="头像" className="round_icon"/>
                            <div className="name"><span>{this.props.user.username}</span></div>
                        </div>
                        :
                        <div className="loginButton">
                            <Button onClick={this.showModal.bind(this)} type="primary" ghost><span>登录</span></Button>
                            
                            <Modal title="登录"
                                visible={this.state.visible}
                                onOk={this.handleOk}
                                // onOk={this.props.login}
                                confirmLoading={this.state.confirmLoading}
                                onCancel={this.handleCancel}
                            >
                            <Tabs defaultActiveKey="1" onChange={this.callback.bind(this)}>
                                <TabPane tab="登陆" key="1">
                                    <div className="loginRegister">
                                        <img src={this.props.user.headimg} alt="用户头像" className="round_icon"/>
                                        <Input placeholder="请输入你的昵称"
                                            className="loginRegisterInput"
                                            prefix={<Icon type="user" />}
                                            name="username" 
                                            onChange={(e) => this.onValueChange(e)} />
                                        <Input placeholder="请输入你的密码"
                                            className="loginRegisterInput2"
                                            prefix={<Icon type="eye"/>}
                                            name="password" 
                                            onChange={(e) => this.onValueChange(e)} />
                                    </div>
                                </TabPane>
                                <TabPane tab="注册" key="2">
                                    <div className="loginRegister">
                                        <img src={this.props.user.headimg} alt="用户头像" className="round_icon"/>
                                        <Input placeholder="注册昵称"
                                            className="loginRegisterInput"
                                            prefix={<Icon type="user" />}
                                            name="registername" 
                                            onChange={(e) => this.onValueChange(e)} />
                                        <Input placeholder="注册密码"
                                            className="loginRegisterInput2"
                                            prefix={<Icon type="eye"/>}
                                            name="registerpwd" 
                                            onChange={(e) => this.onValueChange(e)} />
                                    </div>
                                </TabPane>
                            </Tabs>
                            </Modal>
                        </div>
                    }
                </Card>
            </div>
        )
    }
}

export default Login