import {message} from 'antd';
import axios from 'axios'
const LOGIN = 'LOGIN'
const LOGOUT = 'LOGOUT'
const USER_DATA = 'USER_DATA'

const initState = {
    isAuth:false,
    username:"",
    headimg:getRandomImg()
}

export function user(state=initState,action){
    // console.log(state,action);
    switch(action.type){
        case LOGIN:
            return {...state,isAuth:action.payload.isAuth,username:action.payload.user.username,headimg:action.payload.user.headimg}
        case LOGOUT:
            return {...state,isAuth:action.payload.isAuth,username:action.payload.user.username}
        case USER_DATA:
            return {...state,user:action.payload.user,headimg:action.payload.headimg,isAuth:action.payload.isAuth}
        default:
            return state
    }
}

export function userRegister(data){
    return dispatch=>{
        axios.post("/chat/userregister/",data).then(
            res=>{
                console.log(res.data)
                if(res.status === 200 && res.data.code === 0){
                    message.success("用户名："+res.data.username+"  注册成功!")
                }else if(res.status === 200 && res.data.code === 1){
                    message.warning(res.data.msg)
                    window.location.replace("http://locahost");
                }
            }
        )
    }
}

export function userlogout(data){
    return dispatch=>{
        axios.post('/chat/userlogout/',data).then(
            res=>{
                console.log("退出：",res)
                if (res.status === 200 && res.data.code === 0){
                    message.success("你已退出登陆！");
                    dispatch(user_logout(res.data)); 
                }
            }
        )
    }

}

export function userLogin(data){
    return dispatch=>{
        axios.post("/chat/userlogin/",data).then(res=>{
            if(res.status === 200 && res.data.code === 0){
                 dispatch(login(res.data))
                 message.success("欢迎你："+res.data.user.username+"^.^")
            }else if(res.status === 200 && res.data.code === 1){
                message.warn("TAT,用户已登陆...,请先退出")
                window.location.replace("http://locahost");
            }else if(res.status === 200 && res.data.code === 2){
                //用户名账号或密码错误
                message.error("用户名账号或密码错误")
                window.location.replace("http://locahost");
            }else{
                message.error("服务器内部错误，请联系管理员：18620010839")
                window.location.replace("http://locahost");
            }
        })
    }
}

export function getUserData(){
    //dispatch用来通知数据修改
    return dispatch=>{
        axios.get("/chat/userData/").then(
            res=>{
                if(res.status===200){
                    dispatch(userData(res.data))
                }
            }
        )
    }
}

export function user_logout(data){
    return {type:LOGOUT,payload:data}
}



export function userData(data){
    return {type:USER_DATA,payload:data}
}

//action
export function login(data){
    return {type:LOGIN,payload:data}
}

export function logout(){
    return {type:LOGOUT}
}

function randomForm(lowerValue,upperValue){
    return Math.floor(Math.random()*(upperValue - lowerValue + 1)+lowerValue)
}

