import axios from 'axios';
import React from 'react';
import userHead from '../img/head3.jpg';
import {connect} from 'react-redux';
import {changecurrent,readusermsg} from '../redux/usermsg.redux';
import {changeRoom} from '../redux/chatmsg.redux';
import {Badge} from 'antd'
@connect(
    state=>state,
    {changecurrent,changeRoom,readusermsg}
)
class User extends React.Component{

    constructor(props){
        super(props)
        this.state={
            color:"rgba(160, 241, 247, 0.5)"
        }
    }

    // componentDidUpdate(){
    //     this.props.readusermsg(this.props.friend,this.props.user.username)
    //     let data = {
    //         "myname":this.props.user.username,
    //         "friend":this.props.friend
    //     }
    //     axios.post("/chat/readusermsgs/",data).then(
    //         res=>{
    //             if(res.status === 200 && res.data.code === 0){
    //                 console.log("更新用户信息成功！")
    //             }
    //         }
    //     ) 
    // }

    handleChangeUser(){
        this.props.changecurrent(this.props.friend)
        this.props.changeRoom("")
        let data = {
            "myname":this.props.user.username,
            "friend":this.props.friend
        }

        this.props.readusermsg(this.props.friend,this.props.user.username)

        axios.post("/chat/readusermsgs/",data).then(
            // res=>{
            //     if(res.status === 200 && res.data.code === 0){
            //         console.log("更新用户信息成功！")
            //     }
            // }
        )
    }

    componentDidMount(){
    //     console.log(this.props.usermsg.chatmsg.filter(v=>v.isread===0).length)
    //   const unreadnum = this.props.usermsg.chatmsg.filter(v=>v.isread===0&&v.myname===this.props.friend).length
    //   this.setState({
    //       unread:unreadnum
    //   })
    }


    render(){
        const unreadnum = this.props.usermsg.chatmsg.filter(v=>v.isread===0&&v.myname===this.props.friend).length
        return(
            <div>
                <div className="room" style={{
                    backgroundColor:this.props.usermsg.currentuser === this.props.friend?"rgba(8, 192, 248, 0.5)":"rgba(160, 241, 247, 0.5)" 
                    }} onClick={this.handleChangeUser.bind(this)}>
                    <img src={this.props.friendimg} alt="用户头像"/>
                    <div className="roommsg">
                        <div className="nameandunread">
                            <span>{this.props.friend}</span>
                            {
                                <Badge count={unreadnum}/>
                            }
                            
                        </div>

                        <span>{this.props.lastuser}  {this.props.lastmsg}</span>
                    </div>
                </div>
                <div style={{width:223,height:2,margin:0,padding:0,backgroundColor:"#D5D5D5",overflow:"hidden"}}></div>
            </div>
        )
    }
}

export default User