import axios from 'axios';
import {message} from 'antd';
const CREATE_ROOM = 'CREATE_ROOM';
const GET_ROOMLIST = 'GET_ROOMLIST';
const GET_SEARCHLIST = 'GET_SEARCHLIST';
// const initState1 = [{
//     "roomid":1,
//     "roomname":"聊天广场",
//     "roomimg":getRandomImg(),
//     'lastuser':"dean",
//     'lastmsg':"hello",
//     "msglist":[{"sender":"","ccontent":"","stime":"","senderimg":""}]
// }]
const initRoomList = [{
    "roomid":"",
    "roomname":"",
    "username":"",
    "roomimg":getRoomRandomImg(),
}]



const initState = {
    "roomlist":initRoomList,"searchRoomList":[]
}



export function roomlist(state=initState,action){
    // console.log(state,action)
    switch(action.type){
        case CREATE_ROOM:
            return {...state,roomlist:action.payload.data}
        case GET_ROOMLIST:
            return {...state,roomlist:action.payload.data}
        case GET_SEARCHLIST:
            return {...state,searchRoomList:action.payload.data}
        default:
            return state
    }
}

export function deleteRoom(data){
    return dispatch => {
        axios.post("/chat/deleteroom/",data).then(
            res=>{
                if(res.status === 200 && res.data.code === 0){
                    dispatch(get_roomlist(res.data))
                    message.warning("已退出群聊："+res.data.msg)
                }else{
                    message.error("服务器内部错误")
                }
            }
        )
    }  
}



export function getRoomList(data){
    // console.log("data数据值：",data)
    return dispatch=>{
        axios.get('/chat/getroomlist?user='+data).then(
            res=>{
                if(res.status === 200 && res.data.code === 0){
                    // console.log("打印roomlist:",res.data.data)
                    dispatch(get_roomlist(res.data))
                }
            }
        )
    }
}

export function createRoom(data){
    return dispatch=>{
        axios.post("/chat/createroom/",data).then(res=>{
            // console.log("收到服务端发回来的房间列表：",res.data)
            let roomname = data.roomname;
            if(res.status === 200 && res.data.code === 0){
                dispatch(create_room(res.data))
                message.success("聊天室："+roomname+"-创建成功啦~")
            }else if(res.status === 200 && res.data.code === 1){
                message.warning(res.data.error)
            }
        })
    }
}

export function getSearchRoomList(data){

        return dispatch =>{
            if(data.length === 0){
                message.warning("请输入一个群关键字")
            }else{
                axios.get("/chat/searchroom?roomname="+data).then(res=>{
                    if(res.status === 200 && res.data.code === 0){
                        
                        dispatch(get_searchlist(res.data))
                    }
                });
            }  
        }
}

export function userJoinRoomNewData(data){
    
    return dispatch =>{
        axios.post("/chat/userjoinroom/",data).then(
            res =>{
                console.log("userinroom:",res)
                if(res.status === 200 && res.data.code === 0){
                    dispatch(get_roomlist(res.data))  
                }else if(res.status === 200 && res.data.code === 1){
                    message.warning(res.data.error)
                }else{
                    message.error("服务器内部错误")
                }
            }
        )
    }


}


function get_searchlist(data){
    return {type:GET_SEARCHLIST,payload:data}
}

function get_roomlist(data){
    return {type:GET_ROOMLIST,payload:data}
}

function create_room(data){
    return {type:CREATE_ROOM,payload:data}
}








function randomForm(lowerValue,upperValue){
    return Math.floor(Math.random()*(upperValue - lowerValue + 1)+lowerValue)
}

export function getRoomRandomImg(){
    var index = randomForm(0,5);
    var headImgArry = new Array();
    var headimg0 = require("../img/group1.jpg")
    var headimg1 = require("../img/group2.jpg")
    var headimg2 = require("../img/group3.jpg")
    var headimg3 = require("../img/group4.jpg")
    var headimg4 = require("../img/group5.jpg")
    var headimg5 = require("../img/group6.jpg")
    headImgArry.push(headimg0)
    headImgArry.push(headimg1)
    headImgArry.push(headimg2)
    headImgArry.push(headimg3)
    headImgArry.push(headimg4)
    headImgArry.push(headimg5)

    return headImgArry[index]
}