import React from 'react';
import {Card,Input,Icon,Modal,Tabs,Popover, message,Collapse} from 'antd';
import Room from './room';
import User from './user';
import Searchroom from './searchroom';
import SearchUser from "./searchuser";
import {connect} from 'react-redux';
import {createRoom,getRoomList,getSearchRoomList} from '../redux/roomlist.redux';
import {getsearchuserlist} from "../redux/usermsg.redux";
import {getRoomRandomImg} from '../redux/roomlist.redux';

const Search = Input.Search;
const TabPane = Tabs.TabPane;
const Panel = Collapse.Panel;

@connect(
    state=>state,
    {createRoom,getRoomList,getSearchRoomList,getsearchuserlist}
)
class RoomList extends React.Component{

    constructor(props){
        super(props)
        this.state={
            ModalText: '创建群聊',
            visible: false,
            roomimg:getRoomRandomImg()
        }
    }
    componentDidMount(){
        var data = this.props.user.username;
        this.props.getRoomList(data)
    }
    
    onValueChange(e){
        let name = e.target.name,
            value = e.target.value.trim();
        this.setState({
            [name] : value
        })
    }

    showModal = () => {
        this.setState({
          visible: true,
        });
      }
    handleOk = () => {
        
        if(this.props.user.isAuth){
            
            let roomname = this.state.roomname;
            let username = this.props.user.username;
            let roomimg = this.state.roomimg;
            
            let data = {
                "roomname":roomname,
                "username":username,
                "roomimg":roomimg
            }
            this.props.createRoom(data);
            this.setState({
                confirmLoading: true,
            });
            
            setTimeout(() => {
                this.setState({
                visible: false,
                confirmLoading: false,
                });
            }, 1000);

        }else{
            message.error("请登陆创建群聊~")
            setTimeout(() => {
                this.setState({
                visible: false,
                confirmLoading: false,
                });
            }, 1000);
        }

    }

    handleCancel = () => {
    this.setState({
        visible: false,
    });
    }
    render(){
        const text = <span>搜索</span>;
        const content = (
        <div>
            <Tabs defaultActiveKey="1">
                <TabPane tab="搜索群聊" key="1">
                    <Search
                        placeholder="请输入群名称..."
                        style={{ width: 250 }}
                        onSearch={(value) => this.props.getSearchRoomList(value)}
                    />

                    <Card style={{ width: 300,height:150,overflowY:"auto",overflowX:"hidden",marginTop:10 }}>
                        {
                            this.props.roomlist.searchRoomList.length === 0?
                            <span>输入关键字查询，查询不到请换个关键词，例如："react"。</span>
                            :
                            this.props.roomlist.searchRoomList.map((value,index)=>{
                                return <Searchroom {...value} key={index}></Searchroom>
                            })
                        }
                    </Card>

                </TabPane>
                <TabPane tab="搜索用户" key="2">
                
                    <Search
                            placeholder="请输入用户名称..."
                            style={{ width: 250 }}
                            onSearch={value => this.props.getsearchuserlist(value)}
                    />

                    <Card style={{ width: 300,height:150,overflowY:"auto",overflowX:"hidden",marginTop:10}}>
                            {
                                this.props.usermsg.searchUserList.length === 0?
                                <span>输入关键字查询，查询不到请换个关键词，例如："dean"。</span>
                                :
                                this.props.usermsg.searchUserList.map((value,index)=>{
                                    return <SearchUser {...value} key={index}></SearchUser>
                                })
                            }
                    </Card>

                </TabPane>
            </Tabs>
        </div>
        );
        // this.props.roomlist[0].roomimg
        const customPanelStyle = {
            borderRadius: 4,
            marginBottom: 5,
            border: 0,
            overflow: 'hidden',
            };
        return(
            <div>
            <div   className="searchroom">
                <Popover placement="left" overlayClassName="searchRoomPop" title={text} content={content} trigger="click">
                    <Icon type="search" className="searchIcon"/>
                </Popover>
                <Icon type="plus" onClick={this.showModal.bind(this)}  className="plusroom"/>
                <Modal title="创建群聊"
                        visible={this.state.visible}
                        onOk={this.handleOk}
                        // onOk={this.props.login}
                        confirmLoading={this.state.confirmLoading}
                        onCancel={this.handleCancel}
                    >
                    <div className="loginRegister">
                        <img src={this.state.roomimg} alt="群头像" className="round_icon"/>
                        <Input placeholder="请输入群名称"
                                name="roomname" 
                                onChange={(e) => this.onValueChange(e)} />
                    </div>
                </Modal>
            </div>    
            <div className="roomList">
                <Card style={{ width: 225,height:425 }}>
                    <div className="roomlistscoller">
                    <Collapse defaultActiveKey={['1','2']} bordered={false}>
                    <Panel header="群聊" key="1" style={customPanelStyle}>
                        {
                            this.props.roomlist.roomlist.map((room,index)=>{
                            // console.log("123456",room.roomname,index);
                            return <Room {...room}  key={index}></Room>
                            })  
                        }
                    </Panel>
                    <Panel header="好友" key="2" style={customPanelStyle}>
                        {
                            this.props.usermsg.userlist.map((user,index)=>{
                                return <User {...user} key={index}></User>
                            })
                        }
                    </Panel>
                    </Collapse>
                    </div>
                </Card>
            </div>
            </div>

        )
    }
}
// {...room}
export default RoomList