import React from 'react';
import {Col,Row} from 'antd';
export default class Emoji extends React.Component{
    
    // constructor(props){
    //     super(props)
    // }

    handleEmojiClick(e){
        // console.log(e.currentTarget.getAttribute('data-emoji'));
        let selectEmoji = e.currentTarget.getAttribute('data-emoji');
        this.props.onEmojiChange(selectEmoji);
        
    }

    render(){
        return(
            <div className="emojies">
                <Row type="flex" justify="space-around">
                    {
                        this.props.emoji.map((v,index)=>{
                            return <Col span={3} key={index} data-emoji = {v.text} onClick={(e)=>this.handleEmojiClick(e)}>{v.text}</Col>
                        })
                    }
                </Row>

            </div>
        )
    }
}