import React from 'react';
import {Badge} from 'antd';
import {connect} from 'react-redux';
import {changeRoom,readchatmsg} from '../redux/chatmsg.redux';
import {changecurrent} from '../redux/usermsg.redux';
import axios from 'axios';

@connect(
    state=>state,
    {changeRoom,changecurrent,readchatmsg}
)
class room extends React.Component{


    constructor(props){
        super(props)
        this.state={
            color:"rgba(160, 241, 247, 0.5)"
        }
    }

    handleChangeRoom(){
        this.setState({color:"red"})
        this.props.changeRoom(this.props.roomname)
        this.props.changecurrent("")
        let params = {
            "username":this.props.user.username,
            "roomname":this.props.roomname
        }
        this.props.readchatmsg(this.props.roomname)
        axios.post("/chat/readgroupmsgs/",params).then(
            // res => {
            //     if(res.status === 200 && res.data.code === 0){
                        
            //     }
            // }
        )
    }

    render(){
        const unreadnum = this.props.chatmsg.chatmsg.filter(v=>v.isread===0&&v.destination===this.props.roomname).length
        return(
            <div>
                <div className="room" style={{
                    backgroundColor:this.props.chatmsg.currentroom === this.props.roomname?"rgba(8, 192, 248, 0.5)":"rgba(160, 241, 247, 0.5)" 
                    }} onClick={this.handleChangeRoom.bind(this)}>
                    <img src={this.props.roomimg} alt="群头像"/>
                    <div className="roommsg">
                    <div className="nameandunread">
                            <span>{this.props.roomname}</span>
                            {
                                <Badge count={unreadnum}/>
                            }           
                        </div>
                        <span>{this.props.lastuser}  {this.props.lastmsg}</span>
                    </div>
                </div>
                <div style={{width:223,height:2,margin:0,padding:0,backgroundColor:"#D5D5D5",overflow:"hidden"}}></div>
            </div>
        )
    }
}

export default room