import axios from 'axios';
// import {getRandomImg} from './roomlist.redux';
import {message} from 'antd';
import io from 'socket.io-client';
// const socket = io('ws://139.199.223.247:9002/users');
// const socket = io('ws://192.168.79.128:9002/users');
const socket = io('ws://localhost:9002/chatroom');


const GET_USERMSG = "GET_USERMSG";
const RECV_USERMSG = "RECV_USERMSG";
const GET_USERLIST = "GET_USERLIST";
// const SEND_USERMSG = "SEND_USERMSG";
const CHANGE_CURRENT = "CHANGE_CURRENT";
const GET_FRIENDLIST = 'GET_FRIENDLIST';
const GET_SEARCHUSERLIST = "GET_SEARCHUSERLIST";
const RECV_USERMSGID = "RECV_USERMSGID";
const RECV_OLDUSERMSG = "RECV_OLDUSERMSG";

const initState = {
    chatmsg:[],
    currentuser:"",
    userlist:[],
    searchUserList:[],
    usermsgid:[]
}

export function usermsg(state=initState,action){
    switch(action.type){
        case GET_USERMSG:
            return {...state,chatmsg:action.payload.data}
        case RECV_USERMSG:
            return {...state,chatmsg:[...state.chatmsg,action.payload]}
        case GET_USERLIST:
            return {...state,userlist:action.payload.data}
        case CHANGE_CURRENT:
            return {...state,currentuser:action.payload}
        case GET_FRIENDLIST:
            return {...state,userlist:action.payload}
        case GET_SEARCHUSERLIST:
            return {...state,searchUserList:action.payload.data}
        case RECV_USERMSGID:
            return {...state,usermsgid:action.payload.msgdictid}
        default:
            return state
    }
}


export function deleteUser(data){
    return dispatch =>{
        axios.post("/chat/deleteuser/",data).then(
            res=>{
                if(res.status === 200 && res.data.code === 0){
                    dispatch(get_userlist(res.data))
                    message.warning("删除好友成功："+res.data.msg)
                }else{
                    message.error("服务器内部错误")
                }
            }
        )
    }
}

// 解决方案1. 获取满足条件的数组id，将数组的id值保存到一个新的数组，遍历，该新数组根据索引修改旧数组中的值并且dispatch。
// 解决方案2. 在用户点击对应用户的聊天窗口时，直接再请求一次修改过的msglist，这样做的缺点：服务器的压力增大，不考虑
export function readusermsg(myname,friend){
    return (dispatch,getState)=>{
        let newUserMsg = getState().usermsg.chatmsg;
        newUserMsg.map((v,index)=>{
            if(v.myname === myname && v.friend === friend){
                newUserMsg[index]["isread"] = 1
            }
        });
        let data = {"data":newUserMsg}
        dispatch(get_usermsg(data));
    }
}

export function getusermsglist(data){
    return dispatch=>{
        axios.get("/chat/getusermsglist2?username="+data).then(
            res=>{
                if(res.status === 200 && res.data.code === 0){
                    dispatch(get_usermsg(res.data))
                    dispatch(get_usermsgid(res.data))
                }
            }
        )
    }
}

export function getuserlist(data){
    //data为当前的username
    return dispatch=>{
        axios.get("/chat/getmyusers?myname="+data).then(
            res =>{
                if(res.status === 200 && res.data.code === 0){
                    dispatch(get_userlist(res.data))
                }
            }
        )
    }
}

export function clearFriendList(){
    return dispatch=>{
        dispatch(get_friendlist([]))
    }
}

export function recvNewFriend(){
    return dispatch=>{
        socket.on("recvnewfriend",function(data){
            dispatch(get_friendlist(data))
        })
    }
}

export function getOldMsg(data){
    return (dispatch,getState)=>{
        axios.get("/chat/getoneusermsg?username="+data.username+"&friend="+data.friend+"&lastid="+data.lastid).then(
            res =>{
                if(res.status === 200 && res.data.code === 0){
                    let oldusermsglist = getState().usermsg.chatmsg;
                    let nowusermsglist = res.data.data.concat(oldusermsglist)
                    dispatch(get_usermsg({"data":nowusermsglist}))
                    let msgdictid = getState().usermsg.usermsgid
                    msgdictid[data.friend] = res.data.msgdictid[data.friend]
                    dispatch(get_usermsgid({"msgdictid":msgdictid}))
                    console.log("dictid",msgdictid)
                }else if(res.status === 200 && res.data.code === 1){
                    message.warning("没有更多历史消息可获取了")
                }
            }
        )
    }
}

export function recvMymsg(){
    return (dispatch,getState)=>{
        socket.on("recvusermsg",function(data){
            //获取这天消息是发送给谁的，并且判断当前用户是不是这个朋友，如果是：修改isread为1，并且向后端请求说已经读了，否则不修改也不请求后端
            if(getState().usermsg.currentuser === data.myname){
                data["isread"] = 1
                let params =  {
                            "myname":getState().user.username,
                            "friend":getState().usermsg.currentuser
                        }
                axios.post("/chat/readusermsgs/",params).then(
                    res=>{
                        if(res.status === 200 && res.data.code === 0){
                            console.log("更新用户信息成功！")
                        }
                    }
                )
            }
            dispatch(recv_usermsg(data));
            setTimeout(()=>{document.getElementById('chatListDOM').scrollTop = document.getElementById('chatListDOM').scrollHeight}, 0);
        })
    }
}

export function sendusermsg(source,to,msg){
    // let sortAarry = [source,to].sort()
    // console.log("打印数组的排序结果：",sortAarry)
    return (dispatch,getState)=>{
        socket.emit("sendmsg",{"myname":source,"headimg":getState().user.headimg,"friend":to,"msg":msg})
    }
}

export function changecurrent(data){
    return dispatch=>{
        dispatch(change_currentuser(data))
    }
}

export function getsearchuserlist(data){
    return dispatch=>{
        if(data.length === 0){
            message.warning("请输入关键字...")
        }else{
            axios.get("/chat/getsearchuser?username="+data).then(
                res=>{
                    if(res.status === 200 && res.data.code === 0){
                        console.log("searchuserlist:",res.data)
                        dispatch(get_searchuserlist(res.data))
                    }
                }
            )
        }
    }
}

export function getusermsgid(){
    return dispatch=>{
        socket.on("recvmsgid",function(data){
            dispatch(get_usermsgid(data))
        })
    }
}

function get_usermsgid(data){
    return {type:RECV_USERMSGID,payload:data}
}

function get_searchuserlist(data){
    return {type:GET_SEARCHUSERLIST,payload:data}
}

function get_friendlist(data){
    return {type:GET_FRIENDLIST,payload:data}
}

function change_currentuser(data){
    return {type:CHANGE_CURRENT,payload:data}
}

function get_usermsg(data){
    return {type:GET_USERMSG,payload:data}
}

function get_userlist(data){
    return {type:GET_USERLIST,payload:data}
}

function recv_usermsg(data){
    return {type:RECV_USERMSG,payload:data}
}

function recv_oldusermsg(data){
    return {type:RECV_OLDUSERMSG,payload:data}
}

