import React from 'react';
import {Card,Button,Upload,message,Icon,Collapse,List} from 'antd';
import {connect} from 'react-redux';
import {userlogout} from '../redux/user.redux';
import {getRoomList} from '../redux/roomlist.redux';
import {getOnline,clearFileList,disconnect} from '../redux/chatmsg.redux';
import {clearFriendList,deleteUser} from '../redux/usermsg.redux';
import {deleteRoom} from '../redux/roomlist.redux';
import User from './onlineuser';
const Panel = Collapse.Panel;


@connect(
    state=>state,
    {userlogout,getRoomList,getOnline,clearFriendList,clearFileList,disconnect,deleteRoom,deleteUser}
)
class RoomNoticeOnline extends React.Component{

    constructor(props){
        super(props)
        this.state={
            "filename":"默认值"
        }
    }

    handleLogout(){
        let data = {
            "username":this.props.user.username
        };
        let data2 = "";
        this.props.getRoomList(data2);
        this.props.userlogout(data);
        this.props.clearFriendList();
        this.props.clearFileList();
        window.location.replace("http://locahost");
        // this.props.disconnect();
    }

    handleDeleteRoom(){
        let data = {
            "username":this.props.user.username,
            "roomname":this.props.chatmsg.currentroom
        }
        if(data.username === "" || data.roomname === ""){
            message.error("请登录！")
        }else{
            this.props.deleteRoom(data)
        } 
    }

    handleDeleteFriend(){
        let data = {
            "username":this.props.user.username,
            "friend":this.props.usermsg.currentuser
        }
        if(data.username === ""||data.roomname===""){
            message.error("请登录！")
        }else{
            this.props.deleteUser(data)
        }
    }

    componentDidMount(){
        this.props.getOnline()
    }

    beforeUpload(file){
        this.setState({
            "filename":file.name
        })
        return true
    }

    // callback(key) {
    //     console.log(key);
    // }

    render(){
        const props = {
            name: 'file',
            action: '/chat/uploadfile/',
            headers: {
              authorization: 'authorization-text',
            },
            data:{
                "someone":this.props.chatmsg.currentroom,
                "filename":this.state.filename,
                "username":this.props.user.username
            },
            onChange(info) {
              if (info.file.status !== 'uploading') {
                console.log(info.file, info.fileList);
              }
              if (info.file.status === 'done') {
                message.success(`${info.file.name} 上传成功 ！`);
              } else if (info.file.status === 'error') {
                message.error(`${info.file.name} 上传失败 ！`);
              }
            },
          };
        return(
            
            <div className="onlineNotice">
            <Card style={{ width: 200,height:220,textAlign:'center'}}>
                <div className="noticeMsg">
                    { this.props.user.username && this.props.chatmsg.currentroom !== ""?
                                        <div>
                                        <Upload {...props} beforeUpload={this.beforeUpload.bind(this)}>
                                            <Button ghost>
                                            <Icon type="upload" />上传
                                            </Button>
                                        </Upload>
                                    </div>
                                    :
                                    <span>文件列表</span>
                }

                </div>
                <div className="downloadlist">
                <Collapse>
                    <Panel header="群文件" key="1">
                    <div className="demo-infinite-container">
                            <List
                                dataSource={this.props.chatmsg.filelist}
                                renderItem={item => (
                                <List.Item key={item.id}>
                                    <List.Item.Meta
                                    title={<a href={item.fileurl}>{item.filename}</a>}
                                    />
                                </List.Item>
                                )}
                            >
                            </List>
                    </div>
                    </Panel>
                </Collapse>
                </div>
            </Card>
            <Card style={{ width: 200,height:100 }}>
                <div className="noticeOperation">
                    <p>功能</p>
                </div>
                {
                        this.props.user.isAuth? 
                        <div className="operation">
                            <Button ghost onClick={this.handleLogout.bind(this)}>退出程序</Button>
                            {
                                this.props.chatmsg.currentroom !== ''?
                                <Button ghost onClick={this.handleDeleteRoom.bind(this)}>退出聊天</Button>
                                :
                                <Button ghost onClick={this.handleDeleteFriend.bind(this)}>删除好友</Button>
                            }
                        </div>
                        :
                        ""
                }

            </Card>
            <Card style={{ width: 200,height:240 }}>
                <div className="onlinelist">
                    <div className="noticeOnline">
                        <p>在线列表</p>
                    </div>
                {
                    this.props.chatmsg.online.map((value,index)=>{
                        if(value.room.indexOf(this.props.chatmsg.currentroom) === -1){
                            
                        }else{
                            return <User {...value} key={index}></User>
                        }
                    })
                }
                </div>
            </Card>
        </div>
        );
    }
}
export default RoomNoticeOnline