import React, { Component } from 'react';
import {createStore,applyMiddleware,compose} from 'redux';
import Login from './component/login';
// import SearchRoom from './component/searchAdd';
import MsgPanel from './component/msgPanel';
import RoomNoticeOnline from './component/roomNoticeOnline';
import RoomList from './component/roomLists';
import './App.css';
import thunk from 'redux-thunk';
import {Provider} from 'react-redux';
import reducer from './reducer';


const store = createStore(reducer,compose(
  applyMiddleware(thunk),
  window.devToolsExtension?window.devToolsExtension():f=>f
))
//两个reducers 每一个reducers都有一个state
//合并reducers

class App extends Component {

  render() {

    return (
      <Provider store={store}>
        <div className="main">
          <div className="userAroom">
            <Login></Login>
            <RoomList></RoomList>
          </div>
          <MsgPanel></MsgPanel>
          <RoomNoticeOnline></RoomNoticeOnline>
        </div>
      </Provider>
    );
  }
}

export default App;
