import React from 'react';
import roomHead from '../img/head1.jpg';
import {Button, message}  from 'antd';
import {userJoinRoomNewData} from '../redux/roomlist.redux'
import {connect} from 'react-redux';

@connect(
    state=>state,
    {userJoinRoomNewData}
)
class Searchroom extends React.Component{

    constructor(props){
        super(props)
        this.state={
            color:"rgba(160, 241, 247, 0.5)"
        }
    }

    handleJoinGroup(event){

        let roomname = event.currentTarget.getAttribute("data-roomname");
        let username = event.currentTarget.getAttribute("data-username");
        // console.log("friend:",friend)
        // console.log("roomname",roomname) 不存在 null
        
        if(this.props.user.isAuth){
            let data = {
                "username":username,
                "roomname":roomname
            }
            this.props.userJoinRoomNewData(data)
        }else{
            message.error('请先登录！');
        }





    }
   
    render(){
        return(
            <div>
            <div className="roomsearch" style={{backgroundColor:this.state.color}}>
                <img src={this.props.roomimg} alt="头像"/>
                <div className="roommsg">
                    <span>{this.props.roomname}</span>
                </div>
                <Button className="searchButton" data-roomname={this.props.roomname} data-username={this.props.user.username} onClick={this.handleJoinGroup.bind(this)}>加入群</Button>
            </div>
            <div style={{width:300,height:2,margin:0,padding:0,backgroundColor:"#D5D5D5",overflow:"hidden"}}></div>
            </div>
        )
    }
}
export default Searchroom