import axios from 'axios';
import React from 'react';
import roomHead from '../img/head10.jpg';
import {Button,message}  from 'antd';
import {connect} from 'react-redux';
import {getuserlist} from '../redux/usermsg.redux';


@connect(
    state=>state,
    {getuserlist}
)
class SearchUser extends React.Component{


    constructor(props){
        super(props)
        this.state={
            color:"rgba(160, 241, 247, 0.5)"
        }
    }

    handleSendUser(event){

        let myname = event.currentTarget.getAttribute("data-myname");
        let friend = event.currentTarget.getAttribute("data-username");
        
        let data = {
            "myname":myname,
            "friend":friend
        }

        if(myname === friend){
            message.warning("不能加自己为好友！")
        }else{
            axios.post("/chat/createmyusers/",data).then(
                res=>{
                    if(res.status === 200 && res.data.code === 0){
                        this.props.getuserlist(this.props.user.username)
                    }else if(res.status === 200 && res.data.code === 1){
                        message.warning(res.data.error)
                    }
                }
            )
        }
    }
   
    render(){
        return(
            <div>
            <div className="roomsearch" style={{backgroundColor:this.state.color}}>
                <img src={this.props.headimg} alt="头像"/>
                <div className="roommsg">
                    <span>{this.props.username}</span>
                </div>
                <Button className="searchButton" data-username={this.props.username} data-myname={this.props.user.username} onClick={this.handleSendUser.bind(this)}>发送消息</Button>
            </div>
            <div style={{width:300,height:2,margin:0,padding:0,backgroundColor:"#D5D5D5",overflow:"hidden"}}></div>
            </div>
        )
    }
}
export default SearchUser