import React from 'react';
import {Card,Input,Icon,Popover,Button,message} from 'antd';
import {connect} from 'react-redux';
import Emoji from './emoji';
import {recvMsg,sendMsg,msgList,getOldGroupMsg} from '../redux/chatmsg.redux';
import {sendusermsg,getOldMsg} from '../redux/usermsg.redux';
import axios from 'axios';

@connect(
    state=>state,
    {recvMsg,sendMsg,msgList,sendusermsg,getOldMsg,getOldGroupMsg}
)
class MsgPanel extends React.Component{

    constructor(props){
        super(props)
        this.state = {
            text:"",
            loading:false,
            hasMore:true,
            scoll:"1"
        }
    }

    // componentDidUpdate(){
    //     setTimeout(()=>{document.getElementById('chatListDOM').scrollTop = this.chatListDOM.scrollHeight}, 0);
    // }
    

    setChatListScrollBottom () {
        setTimeout(()=>{document.getElementById('chatListDOM').scrollTop = this.chatListDOM.scrollHeight}, 0);
    }

    onScrollHandle(event) {
        // const clientHeight = event.target.clientHeight
        const scrollHeight = event.target.scrollHeight
        const scrollTop = event.target.scrollTop
        // const isBottom = (clientHeight + scrollTop === scrollHeight)
        event.scrollTop = scrollHeight
        // console.log(clientHeight,scrollTop,scrollHeight)
        // console.log('is bottom:' + isBottom)
        if(scrollTop === 0){
            //滚轮到了最上面，往后端请求数据，用msgid来取值
            if(this.props.usermsg.currentuser !== ""){
                let currentuser = this.props.usermsg.currentuser;
                let lastid = this.props.usermsg.usermsgid[currentuser];
                let data = {
                    "username":this.props.user.username,
                    "friend":this.props.usermsg.currentuser,
                    "lastid":lastid
                }

                this.props.getOldMsg(data)
            }else if(this.props.chatmsg.currentroom !== ""){
                let roomname = this.props.chatmsg.currentroom;
                let lastid = this.props.chatmsg.groupmsgid[roomname]
                let data = {
                    "roomname":roomname,
                    "username":this.props.user.username,
                    "lastid":lastid
                }
                this.props.getOldGroupMsg(data)
            }
        }

      }

    

    componentWillMount(){
        this.props.msgList()
    }

    componentWillUnmount() {
        if (this.chatListDOM) {
          this.chatListDOM.removeEventListener('scroll', this.onScrollHandle.bind(this));
        }
      }

    componentDidMount(){
        this.props.recvMsg()
        // this.props.recvMymsg(this.props.user.username)
        if (this.chatListDOM) {
            this.chatListDOM.addEventListener('scroll', this.onScrollHandle.bind(this));
        }
    }

    handleSubmit(){

        if(this.props.user.username !== ""){
            if(this.props.usermsg.currentuser === ""){
                this.props.sendMsg(this.props.user.username,this.props.chatmsg.currentroom,this.state.text)
            }else{
                this.props.sendusermsg(this.props.user.username,this.props.usermsg.currentuser,this.state.text)
            }
    
            this.setState({
                text:" "
            })
        }else{
            message.error('请先登陆，^.^');
        }

    }

    onValueChange(e){
        let name = e.target.name,
            value = e.target.value.trim();
        this.setState({
            [name] : value
        })
    }

    onEmojiChange(value){
        console.log(value);
        this.setState({
            text:this.state.text + value
        })
    }

    Enterkeypress(e){
        if (e.keyCode === 13) {
            if(this.state.text.length === 0){
                alert("发送的消息不能为空！")
            }else{
                this.handleSubmit()  
            }
        }
    }

    sendImg(){
        let formData = new FormData();
        let Imginput = document.getElementById('tupian');
        let file = Imginput.files[0];

        let tosonmeone = this.props.chatmsg.currentroom;
        let tofriend = this.props.usermsg.currentuser;

        // formData.append(file.name, file);
        formData.append('emojiImg',file);
        formData.append("headimg",this.props.user.headimg);
        formData.append('someone',tosonmeone);
        formData.append('imgname',file.name);
        formData.append('username',this.props.user.username);
        formData.append("friend",tofriend);
        
        console.log(formData.get("friend"));
        // console.log(file.name);
        let config = {
            headers: {'Content-Type': 'multipart/form-data'}
        };

        
        axios.post('/chat/uploademoji/',formData, config).then(
            res => {
            if (res.data.code === 0 && res.status===200){
                    console.log(res.data.data)
                }
        })
    }


    render(){
        const login = this.props.user.username
        const emoji = "😀 😁 😂 🤣 😃 😄 😅 😆 😉 😊 😋 😎 😍 😘 😗 😙 😚 🙂 😚 🤗 🤩 🤔 🤨 😐 😑 😶 🙄 😏 😣 😥 😮 🤐 😯 😪 🤑 😨 👻 👽 😈 🤓 👦 👨 👧 👩 🙏 😱 💪 👈 👉 👆 👇 ✋ 👌 👍 👎 ✊ 👋 👏 🙌 👐 💅 👂 👃 👓"
                        .split(' ')
                        .filter(v=>v)
                        .map(v=>({text:v}));
        const text = <span>表情包</span>;
        const content = (
            <div>
                <input type="file" id="tupian"/>
                <Button type="primary" icon="upload" size={"small"} onClick={this.sendImg.bind(this)}>发送</Button>
            </div>
        );

        const emojiTitle = <span>表情</span>;
        const emojiContent = (
        <div>
            <Emoji emoji={emoji} 
                name="emoji"
                value={this.state.currentEmoji}
                onEmojiChange={(value) => this.onEmojiChange(value)}>
            </Emoji>
        </div>
        );
        return(
            <div className="center">
            <div className="roomname">
                <Card style={{ width: 450}}>
                    <p>{
                            this.props.chatmsg.currentroom === ""?
                            this.props.usermsg.currentuser
                            :
                            this.props.chatmsg.currentroom
                        }</p>
                </Card>
            </div>
            <div  className="msgMain">
                <Card style={{ width: 450,height: 473}}>
                <div id="chatListDOM" ref={input => this.chatListDOM = input} className="msglist">
                        {
                            this.props.chatmsg.currentroom !== ""
                            ?
                            this.props.chatmsg.chatmsg.map((msg,index)=>{
                                if(msg.destination === this.props.chatmsg.currentroom){
                                    return msg.source === this.props.user.username?

                                            <div className="mymsgdetail" key={index}>
                                                <div className="mymsg">
                                                    <div className="mymsgdetailuser">
                                                        <p>{msg.ctime}{" "}{msg.source}</p>
                                                    </div>
                                                    <div className="mymsgdetailcontent">
                                                        {
                                                            msg.isimg === 1 ?
                                                            <img width='200px' height='200px' src={msg.msg} alt="头像"/>
                                                            :
                                                            <p>{msg.msg}</p>
                                                        } 
                                                    </div>
                                                </div>
                                                <img src={msg.headimg} alt="用户头像" className="round_icon"></img>
                                            </div>
                                            
                                            :

                                            <div className="othermsgdetail" key={index}>
                                                <img src={msg.headimg} alt="用户头像" className="round_icon"></img>
                                                <div className="othermsg">
                                                    <div className="msgdetailuser">
                                                        <p>{msg.source}{" "}{msg.ctime}</p>
                                                    </div>
                                                    <div className="msgdetailcontent">
                                                        {
                                                            msg.isimg === 1 ?
                                                            <img width='200px' height='200px' src={msg.msg} alt="头像"/>
                                                            :
                                                            <p>{msg.msg}</p>
                                                        } 
                                                    </div>
                                                </div>
                                            </div>
                                }else{
                                    return ""
                                }
                            })
                            :
                            this.props.usermsg.chatmsg.map((msg,index)=>{
                                if(msg.friend === this.props.usermsg.currentuser || msg.myname === this.props.usermsg.currentuser){
                                    return msg.myname === this.props.user.username?

                                            <div className="mymsgdetail" key={index}>
                                                <div className="mymsg">
                                                    <div className="mymsgdetailuser">
                                                        <p>{msg.ctime}{" "}{msg.myname} </p>
                                                    </div>
                                                    <div className="mymsgdetailcontent">
                                                        {
                                                            msg.isimg === 1 ?
                                                            <img width='200px' height='200px' src={msg.msg} alt="头像"/>
                                                            :
                                                            <p>{msg.msg}</p>
                                                        }
                                                    </div>
                                                </div>
                                                <img src={this.props.user.headimg} alt="用户头像" className="round_icon"></img>
                                            </div>
                                            
                                            :

                                            <div className="othermsgdetail" key={index}>
                                                <img src={msg.headimg} alt="用户头像" className="round_icon"></img>
                                                <div className="othermsg">
                                                    <div className="msgdetailuser">
                                                        <p>{msg.myname}{" "}{msg.ctime}</p>
                                                    </div>
                                                    <div className="msgdetailcontent">
                                                        {
                                                            msg.isimg === 1 ?
                                                            <img width='200px' height='200px' src={msg.msg} alt="头像"/>
                                                            :
                                                            <p>{msg.msg}</p>
                                                        }
                                                    </div>
                                                </div>
                                            </div>
                                }
                            })
                        }
                </div>
                </Card>
            </div>
            <div className="msginput">
                {
                    login === ""?
                    <div className="msginputcontent">
                        <div className="emojiOperation">
                            <i className="emojiIcon">                
                                <Icon type="smile" theme="outlined"/> 
                            </i>
                            <i className="emojiIcon">
                                <Icon type="picture" theme="outlined" />
                            </i>
                        </div>
                        <Input placeholder="请登陆" name="text" onChange={(e) => this.onValueChange(e)} value={this.state.text} disabled/>
                        <div className="unsendimg" onClick={this.handleSubmit.bind(this)}>
                            <img src={require("../img/send.png")} alt="发送" className="round_icon"/>
                        </div>
                    </div> 
                    :
                    <div className="msginputcontent">
                        <div className="emojiOperation">
                            <i className="emojiIcon">                
                                <Popover placement="top" title={emojiTitle} content={emojiContent} trigger="click">
                                        <Icon type="smile" theme="outlined"/>
                                </Popover>   
                            </i>
                            <i className="emojiIcon">
                                <Popover placement="top" title={text} content={content} trigger="click">
                                    <Icon type="picture" theme="outlined" />
                                </Popover>
                            </i>
                        </div>
                        <Input placeholder="请输入消息" name="text" onChange={(e) => this.onValueChange(e)} value={this.state.text} onPressEnter={(e)=>this.Enterkeypress(e)} />
                        <div className="sendimg" onClick={this.handleSubmit.bind(this)}>
                            <img src={require("../img/send.png")} alt="发送" className="round_send_icon"/>
                        </div>
                    </div>
                }
            </div>
        </div>
        )
    }
}

export default MsgPanel
