import io from 'socket.io-client';
import axios from 'axios';
import {notification,message} from 'antd';
// const socket = io('ws://139.199.223.247:9002/chatroom');
// const socket = io('ws://192.168.79.128:9002/chatroom');
const socket = io('ws://localhost:9002/chatroom');
// var socket = io('ws://192.168.79.128:9002/chatroom');
const RECV_MSG = "RECV_MSG";
const MSG_LIST = "MSG_LIST";
const CHANGE_ROOM = "CHANGE_ROOM";
// const SEND_LOGIN = "SEND_LOGIN";
const GET_ONLINE = "GET_ONLINE";
const GET_CHATMSG = "GET_CHATMSG";
const GET_FILELIST = "GET_FILELIST";
const REV_FILE = "REV_FILE";
const USER_CLICK_LOGOUT = "CLICK_LOGOUT";
const GET_GROUPMSGID = "GET_GROUPMSGID";

const initState = {
    "chatmsg":[],
    "currentroom":"聊天大厅",
    "online":[],
    "filelist":[],
    "logout":false,
    "groupmsgid":{}
}
//laskackmsg: 

export function chatmsg(state=initState,action){
    // console.log(state,action)
    switch(action.type){
        case MSG_LIST:
            // return {...state,users:action.payload.users,chatmsg:action.payload.msgs.filter(v=>!v.read&&v.to==action.payload.userId).length} //未读消息数量
            return {...state,chatmsg:action.payload}
        case RECV_MSG:
            // return {...state,chatmsg:[...state.chatmsg,action.payload]}
            return {...state,chatmsg:[...state.chatmsg,action.payload]}
        case CHANGE_ROOM:
            return {...state,currentroom:action.payload}
        case GET_ONLINE:
            return {...state,online:action.payload}
        case GET_CHATMSG:
            return {...state,chatmsg:action.payload.data}
        case REV_FILE:
            return {...state,filelist:[...state.filelist,action.payload]}
        case GET_FILELIST:
            return {...state,filelist:action.payload.data}
        case USER_CLICK_LOGOUT:
            return {...state,logout:action.payload}
        case GET_GROUPMSGID:
            return {...state,groupmsgid:action.payload.groupmsgid}
        default:
            return state
    }
}

function change_loginstatus(data){
    return {type:USER_CLICK_LOGOUT,payload:data}
}

function get_online(data){
    return {type:GET_ONLINE,payload:data}
}

function change_room(roomname){
    return {type:CHANGE_ROOM,payload:roomname}
}

function msg_list(msgs){
    return {type:MSG_LIST,payload:msgs}
}

function msg_Recv(msg){
    return {type:RECV_MSG,payload:msg}
}

function get_chatmsg(msg){
    return {type:GET_CHATMSG,payload:msg}
}

// function send_login(){
//     return {type:SEND_LOGIN,payload:""}
// }

function get_filelist(data){
    return {type:GET_FILELIST,payload:data}
}

function recv_filelist(data){
    return {type:REV_FILE,payload:data}
}

function noticeUserOnline(data){
    notification['info']({
      message: '用户上线通知',
      description: data,
      duration:2
    });
};

export function recvOnlineNotice(){
    return (dispatch) => {
        socket.on("recvonlinenotice",function(data){
        console.log(data)
        noticeUserOnline(data)
    })}
}

export function disconnect(){
    return (dispatch) =>{
        socket.disconnect()
        dispatch(change_loginstatus(true))
    }
}

export function getFileList(){
    return (dispatch)=>{
        socket.on("recvfile",function(data){
            dispatch(recv_filelist(data))
        })
    }
}

export function clearFileList(){
    return dispatch=>{
        dispatch(get_filelist({"data":[]}))
    }
}

export function getGroupFileList(data){
    return (dispatch)=>{
        axios.get("/chat/getfilelist?username="+data).then(
            res=>{
                if(res.status === 200 && res.data.code === 0){
                    dispatch(get_filelist(res.data))
                }
            }
        )
    }
}

export function readchatmsg(roomname){
    return (dispatch,getState)=>{
        let newChatMsg = getState().chatmsg.chatmsg;
        newChatMsg.map((v,index)=>{
            if(v.destination === roomname){
                newChatMsg[index]["isread"] = 1
            }
        });
        let data = {"data":newChatMsg}
        dispatch(get_chatmsg(data));
    }
}

export function getOnline(){
    return (dispatch,getstate)=>{
        socket.on("online",function(data){
            dispatch(get_online(data))
        })
    }
}

export function changeRoom(data){
    return dispatch=>{
        dispatch(change_room(data))
    }
}

