export function msgList(data){
    return (dispatch,getstate)=>{
        socket.on("msglist",function(data){
            // console.log("test：",data);
            dispatch(msg_list(data))
        })
    }
}

export function recvMsg(){
    return (dispatch,getState)=>{
        socket.on("recvmsg",function(data){
            //判断当前的房间名是不是收到的房间名，如果是就更新我自己的lastackmsgid，否则就不更新
            if(getState().chatmsg.currentroom === data.destination){
                let params = {
                    "username": getState().user.username,
                    "roomname": getState().chatmsg.currentroom
                }
                axios.post("/chat/readgroupmsgs/",params).then(
                    res => {
                        if(res.status === 200 && res.data.code === 0){
                            console.log("通知服务端我已经读了消息！:",res.data.data)
                        }
                    }
                )
            }
            console.log("recvmsg",data)
            if(getState().chatmsg.currentroom === data.destination){
                data["isread"] = 1
            }
            dispatch(msg_Recv(data))
            setTimeout(()=>{document.getElementById('chatListDOM').scrollTop = document.getElementById('chatListDOM').scrollHeight}, 0);
        }) 
    }
}

export function sendMsg(source,roomname,msg){
    return (dispatch,getState)=>{
        socket.emit("sendmsg",{"source":source,"headimg":getState().user.headimg,"destination":roomname,"msg":msg})
    }
}

export function getOldGroupMsg(data){ 
    return (dispatch,getState)=>{
        axios.get("/chat/getonegroupmsglist?username="+data.username+"&roomname="+data.roomname+"&lastid="+data.lastid).then(
            res=>{
                if(res.status === 200 && res.data.code === 0){
                    let oldgroupmsglist = getState().chatmsg.chatmsg;
                    let nowgroupmsglist = res.data.data.concat(oldgroupmsglist)
                    dispatch(get_chatmsg({"data":nowgroupmsglist}))
                    let groupmsgid = getState().chatmsg.groupmsgid
                    groupmsgid[data.roomname] = res.data.groupmsgid[data.roomname]
                    dispatch(get_groupmsgid({"groupmsgid":groupmsgid}))
                }else if(res.status === 200 && res.data.code === 1){
                    message.warning("没有更多消息可以获取了")
                }
            }
        )
    }
}

export function getchatmsglist(data){
    return dispatch=>{
        axios.get("/chat/getchatmsglist2?username="+data).then(
            res=>{
                if(res.status === 200 && res.data.code === 0){
                    dispatch(get_chatmsg(res.data))
                    dispatch(get_groupmsgid(res.data))
                }
            }
        )
    }
}

function get_groupmsgid(data){
    return {type:GET_GROUPMSGID,payload:data}
}

